import { Component, OnInit, Injector, HostListener, ViewChild } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';
import * as moment from 'moment';
import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';
import { CommonConfirmModalComponent, ConfirmDialogModel } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-my-visitor',
  templateUrl: './my-visitor.component.html',
  styleUrls: ['./my-visitor.component.scss']
})

export class MyVisitorComponent implements OnInit {
  
  visitorData:string = "";
  visitorListData:any;

  isVisitorDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  visitor:any;

  modalService:any;

  totalFilterItems:any;

  apartmentUnitId:any;

  visitTypeData:any;
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    private injector: Injector,
  	private apartmentService:ApartmentService,
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService: CookieService,
    private dialog: MatDialog,
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  onGlSearchFilter() {

    if(this.visitorData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.visitorData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  getVisitType(id){

  	 var data = _.filter( this.visitTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'MyVisitorData');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  @HostListener('window:showConfirmModal', ['$event.detail'])
  showConfirmModal(detail) {
    
    let data = this.datagrid.getrowdata(detail.rowId);

    const message = `Are you sure want to delete?`;

    const dialogData = new ConfirmDialogModel("Confirm Action", message);

    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let type = data.type;
        let serviceName;
        if (type == 'Planned Visitor') {
          var params = {
            expectedVisitorId: data.expectedVisitorId,
            deleteBy: parseInt(this.cookieService.get('userId'))
        }
          serviceName = this.visitorService.deleteExpectedVisitorById(params)
        } else if (type == 'UnPlanned Visitor') {
          var params_ = {
            visitorId: data.visitorId,
            deleteBy: parseInt(this.cookieService.get('userId'))
        }
          serviceName = this.visitorService.deleteVisitorById(params_)
        }
       
        this.isVisitorDataLoaded = false;

        serviceName.subscribe((res:any) => {
          this.visitorService.getAllUnplannedVisitors().subscribe((res:any) => {

            let data1 = res.filter(data => {
              data.type = 'UnPlanned Visitor';
              return data.isActive && data.apartmentUnitId == this.apartmentUnitId;
            });
      
      
            this.visitorService.getAllPlannedVisitors().subscribe((res:any) => {
      
              let data2 = res.filter(data => {
                data.type = 'Planned Visitor';
                return data.isActive && data.apartmentUnitId == this.apartmentUnitId;
              });

              let param = {
                apartmentUnitId: this.apartmentUnitId
              }
              this.visitorService.getExpectedVisitorsByApartmentUnitId(param).subscribe((res:any) => {
               
                let data3 = res.filter(data => {
                  data.visitorName = data.expectedVisitorName;
                  data.visitorPhone = data.expectedVisitorPhone;
                  data.visitorInTime = data.expectedVisitorInTime;
                  data.visitorOutTime = data.expectedVisitorOutTime;
                  data.type = 'Planned Visitor';
                  data.showEdit = true;
                  return data.isActive;
                });
      
                let data_ = data2.concat(data1);
              
              this.visitorListData = data3.concat(data_);
             
              this.gridSourceData = {
                localdata: this.visitorListData,
                datatype: "array"
              }
          
              this.listData = new jqx.dataAdapter(this.gridSourceData);
      
              this.totalItems = this.visitorListData.length;
      
              this.isVisitorDataLoaded = true;
      
            }, error => {
      
            });
      
      
            }, error => {
      
            });
      
            }, error => {
      
          });

        }, error => {
          console.log(error);
        });
      }
    });
  }

  @HostListener('window:navigateTo', ['$event.detail'])
  navigateTo(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let expectedVisitorId = dataRecord.expectedVisitorId
    this.router.navigateByUrl('/user/visitor/edit-visitor/' + expectedVisitorId);

  }

  getDateTime(val){
    if(val != ''){
      return moment(val).format("MM/DD/YY hh:mm")
    }else{
      return ''
    }
  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Type',
          datafield: 'type',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      }, {
          text: 'Visitor Name',
          datafield: 'visitorName',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Phone Number',
         datafield: 'visitorPhone',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'In-time',
         datafield: 'visitorInTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Out-time',
         datafield: 'visitorOutTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Visit Type',
         datafield: 'visitTypeId',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getVisitType(value) +'</div>'
        },
         minwidth: 80,
         renderer: columnrenderer
     },{
      text: 'Actions',
      cellsalign: 'center', 
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        let data = this.visitorListData[row];
        if(data.type == 'Planned Visitor' && data.showEdit){
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="navigateTo('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmModal('+ row +')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>'
        }else{
          return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="showConfirmModal('+ row +')"><i class="fa fa-trash icon delete" aria-hidden="true"></i></a></div>' 
        }
      },
      renderer: columnrenderer
  }];
     
    let apartmentUnitParams = {
      userId: parseInt(this.cookieService.get('userId'))
    }

  	this.apartmentService.getApartmentBlockUnitByUserId(apartmentUnitParams).subscribe((res:any) => {

    this.apartmentUnitId = res[0].apartmentBlockUnitId;
    
    let params = {
      apartmentUnitId: this.apartmentUnitId
    }
    console.log(this.apartmentUnitId);

		this.visitorService.getAllUnplannedVisitors().subscribe((res:any) => {

      let data1 = res.filter(data => {
        data.type = 'UnPlanned Visitor';
        return data.isActive && data.apartmentUnitId == this.apartmentUnitId;
      });


      this.visitorService.getAllPlannedVisitors().subscribe((res:any) => {

        let data2 = res.filter(data => {
          data.type = 'Planned Visitor';
          data.visitorId = null;
          return data.isActive && data.apartmentUnitId == this.apartmentUnitId;
        });

        this.visitorService.getExpectedVisitorsByApartmentUnitId(params).subscribe((res:any) => {
         
          let data3 = res.filter(data => {
            data.visitorName = data.expectedVisitorName;
            data.visitorPhone = data.expectedVisitorPhone;
            data.visitorInTime = data.expectedVisitorInTime;
            data.visitorOutTime = data.expectedVisitorOutTime;
            data.type = 'Planned Visitor';
            data.visitorId = null;
            data.showEdit = true;
            return data.isActive;
          });

          let data_ = data2.concat(data1);
        
        this.visitorListData = data3.concat(data_);
       
        this.gridSourceData = {
          localdata: this.visitorListData,
          datatype: "array"
        }
    
        this.listData = new jqx.dataAdapter(this.gridSourceData);

        this.totalItems = this.visitorListData.length;

      

        this.isVisitorDataLoaded = true;

      }, error => {

      });


      }, error => {

      });

      }, error => {

    });

	},
	error => {

	}
  );
  
  let visitParams = {
    LookupTypeId: 15
  }

  	//get visit type
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {

      this.visitTypeData = res;

    },
    error => {
      
    }
    );
 	

  }

}

function navigateTo(row){
  var event = new CustomEvent('navigateTo', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).navigateTo = navigateTo

function showConfirmModal(row){
  var event = new CustomEvent('showConfirmModal', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmModal = showConfirmModal